@use '../../../../variables' as variables;

// === MIXINS ===
@mixin flex-column($gap: 0) {
  display: flex;
  flex-direction: column;
  gap: $gap;
}

@mixin flex-row($gap: 0, $align: center, $justify: space-between) {
  display: flex;
  flex-direction: row;
  align-items: $align;
  justify-content: $justify;
  gap: $gap;
}
:host {
  // display: contents;
  width: 100%;
}
.tuition-receipt-header {
  display: flex;
  align-items: center;
  gap: variables.$global-light-spacing-1;
}

.pt-icon {
  transform: scale(1.4);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.back-icon {
  color: white;
  font-size: 32px;
  height: 32px;
  width: 32px;
  cursor: pointer;
}
// === BASE CONTAINERS ===

.my-tuitions-outer-container {
  @include flex-row(0, flex-start, center);
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  padding: variables.$global-light-spacing-4;
  background-color: variables.$global-light-gray-gray-2;
}

.tuition-card-container {
  @include flex-column(variables.$global-light-spacing-5);
  height: min-content;
  border-radius: 12px;

  width: 100%;
  // background-color: variables.$global-light-gray-gray-1;
  background-image: var(--cardBackground);
  padding: 2.5rem variables.$global-light-spacing-4;
}

.card-border-gradient {
  padding: 1px;
  border-radius: 12px;
  background-image: var(--cardBorderGradient);
  // max-width: 640px;
  box-shadow: var(--boxShadow);
}

// === TOP SECTION ===

.tcc-top {
  h5 {
    color: var(--neutralColor1, variables.$global-light-gray-gray-10);
  }
  @include flex-row();
}

// === BUTTON STYLES ===

.btn-no-borders {
  @include flex-row(variables.$global-light-spacing-1);
  color: variables.$global-light-daybreak-blue-blue-6;
}

.btn-nb-icon {
  transform: scale(0.8);
}

// === CONTENT ===

.tcc-content {
  .body1 {
    color: var(--neutralColor5);
  }
  .table-numeric-align:not(.color-gray-9) {
    color: var(--neutralColor1, variables.$global-light-gray-gray-10);
  }
  .tuition-items-table th {
    color: var(--neutralColor3, variables.$global-light-gray-gray-7);
  }

  @include flex-column(variables.$global-light-spacing-4);
}

.tcc-content-info {
  @include flex-column(variables.$global-light-spacing-2);
}

.tcc-content-info-row {
  @include flex-row(0, center, space-between);
}

// === TABLE STYLES ===

.tuition-items-table {
  th {
    // color: variables.$global-light-gray-gray-7;
    color: red;
    text-transform: uppercase;
    font-size: variables.$global-light-font-size-0;
    letter-spacing: 0.8px;
    text-align: right;
    padding-bottom: variables.$global-light-spacing-1;
    font-family: variables.$global-light-font-families-main;
    padding- &:not(.table-numeric-align) {
      text-align: left;
    }
    padding-right: variables.$global-light-spacing-2;
    padding-left: variables.$global-light-spacing-2;

    &:last-child {
      padding-right: 0px;
    }
    &:first-child {
      padding-left: 0px;
    }
  }
  th.item-header {
    text-align: left;
  }
  .table-numeric-align {
    text-align: right;
  }

  .row-content {
    td {
      padding: variables.$global-light-spacing-2
        variables.$global-light-spacing-2;

      span {
        white-space: nowrap;
      }

      &:first-child {
        padding-right: variables.$global-light-spacing-3;
        padding-left: 0px;
      }

      &:last-child {
        padding-right: 0px;
        // padding-left: 0px;
      }
    }

    .bold {
      font-family: variables.$global-light-font-families-main;
    }
    .total-sum {
      color: var(--primaryColor);
    }
  }
}

// === EXTRA COMPONENTS ===

.division-line {
  height: 1px;
  width: 100%;
  background-color: var(--secondaryColor2, variables.$global-light-gray-gray-4);
}

.item-validity-container {
  @include flex-column(variables.$global-light-spacing-0);

  .body3 {
    display: none;
  }

  .body2 {
    color: var(--neutralColor5);
  }
}

.color-gray-9.body2 {
  color: var(--neutralColor5);
}
.color-gray-10.body2 {
  color: var(--neutralColor1, variables.$global-light-gray-gray-10);
}

.validity-column {
  min-width: 140px;
  color: var(--neutralColor5);
}

// === BOTTOM ===

.tcc-bottom {
  display: flex;
  justify-content: flex-end;

  button {
    width: 260px;
  }
}

// === MEDIA QUERIES ===

@media (max-width: 1200px) {
  .card-border-gradient {
    max-width: none;
  }
}

@media (max-width: 580px) {
  .pt-icon {
    transform: scale(1);
  }
  .tuition-card-container {
    padding: variables.$global-light-spacing-4 variables.$global-light-spacing-3;

    h5 {
      font-size: variables.$global-light-font-size-3;
    }
  }

  .btn-no-borders > p {
    display: none;
  }

  .tuition-items-table .row-content td:first-child {
    padding-right: variables.$global-light-spacing-1;
  }

  .tuition-items-table .row-content td {
    padding: variables.$global-light-spacing-0 0;
  }

  .item-validity-container {
    .body3 {
      display: block;
    }
  }

  .validity-column {
    display: none;
  }
}

@media (max-width: 420px) {
  .tcc-bottom button {
    width: 100%;
  }
}
